$infobar: "infobar";

.#{$infobar}{
    background-color: theme('color.white'); position: relative;  border-radius: theme('radius.0'); box-shadow: theme('boxShadow.all'); padding: theme('padding-6'); margin-bottom: theme('margin-4'); 
    &__media{
        &-icon{width: 3.5rem; height: 3.5rem; border-radius: theme('radius.round');display: flex; align-items: center; justify-content: center;overflow: hidden; position: relative;
            &:before{position: absolute; left: 0; right: 0; top: 0; bottom: 0; content: "";opacity: 0.1;}
        }
        &-icon--alert{color:theme('color.yellow'); font-size: theme('font.size.xxxl'); font-weight: 700;
            &:before{ background-color: theme('color.yellow');}
        }
        &-icon--vcamera{color:theme('color.secondary'); overflow: visible; background-color: #fff;
            
            &:after{
                content: "";
                position: absolute;
                height: 19px;
                width: 19px;
                background: rgba(255, 255, 255, 1);
                border-radius: 50%;
                #{theme('direction.left','value')}: 15px;
                animation: live 1s ease-in-out infinite;
                pointer-events: none;
            }
            .icon--vcamera{
                position: relative;
                z-index: 1;
            }

            @keyframes live {
                0% {
                transform: scale(2,2);
                }
                100% {
                transform: scale(3.5,3.5);
                background: rgba(255, 3, 0, 0);
            }
        }
        }
        &-icon--tick{color:theme('color.primary');
            &:before{position: absolute; left: 0; right: 0; top: 0; bottom: 0; content: "";opacity: 0.1;background-color: currentColor;}
        }
    }
    @media(max-width:theme('breakpoints.sm','value')){
        & {padding:theme('padding-4');
            &__content{padding-bottom: 15px;}
            & .profile-progress{margin-top: 20px; border-top: 1px solid theme('color.gray.200');padding-top: theme('padding.4');}
        }
    }

    @media(max-width:theme('breakpoints.lg','value')){
        &{padding: theme('padding-4'); 
      
        }
    }

}

$infobar__media-icon--tick: "infobar__media-icon--tick";

.#{$infobar__media-icon--tick} {
    &:after{
        content: "";
        position: absolute;
        transform: rotate(45deg);
        height: 17px;
        width: 9px;
        border-bottom: 2px solid currentColor;
        border-right: 2px solid currentColor;
        #{theme('direction.left','value')}: 20px;
        top: 13px;
    }
}

$content-panel: "content-panel";

.#{$content-panel}{position: relative;
    &__head{padding: theme('padding-6'); position: relative;}
    table th{background-color:rgba(0, 0, 0, 0.02) ;}
}

$action-bar: "action-bar";
$social-button: "social-button";
.#{$action-bar}{
    position: relative;background-color: rgba(0, 0, 0, 0.02); padding: theme('padding.6'); border-bottom: 1px solid theme('color.gray.200');border-top: 1px solid theme('color.gray.200');
    @media(max-width:theme('breakpoints.sm','value')){
        &{
            &__content{padding-bottom: theme('padding.6');}
            .#{$social-button}{width: 100%;justify-content: space-between; }
        }
    }
    
}


$g-circle: "g-circle";

.#{$g-circle}{position: relative; width:4rem; height:4rem; background-color: theme('color.white'); box-shadow: theme('boxShadow.all'); border-radius: theme('radius.round'); display: flex; align-items: center; justify-content: center;
    
}

$custom-cols: "custom-cols";

.#{$custom-cols}{
    > ul{display: flex;align-items: flex-start; margin: 0 -10px;
        > li{width: 100%;padding: 0 10px;}
    }

    &--onethird{
        ul{
            & li:first-child{max-width: 20%;}
        }
    }  
    @media(max-width:theme('breakpoints.md','value')){
    &--onethird{
            ul{
                & li:first-child{max-width:35%;}
            }
        }  
    }
}


$colum-layout: "colum-layout";

.#{$colum-layout}{
    @media(min-width:theme('breakpoints.md','value')){
        &{display:grid; grid-gap:20px; grid-template-columns: 49% 49%;}
    }
    @media(max-width:theme('breakpoints.md','value')){
        &__cell{margin-bottom: theme('margin.6');}
    }
    

    


    &__cell{border: 1px solid theme('color.gray.200'); }
    &__head{padding: theme('padding.6') theme('padding.6'); border-bottom: 1px solid theme('color.gray.200');}
    &__scroll{max-height:32.2rem;}
}


$selection: "selection";

.#{$selection}{position: relative;border-bottom: 1px solid theme('color.gray.200'); 
   &__trigger{display: flex;flex-wrap: wrap; position: relative; padding: theme('padding.4') theme('padding.6'); min-height: 3.5rem;cursor: pointer;background: #fff;z-index: 1;

     &-input, input{position: absolute; opacity: 0;}
     &-action{display: flex;align-items: center; justify-content: space-between; width: 100%;color: theme('color.gray.800');}
     &-icon{position: relative;width: 16px; height: 16px;display: block; border-radius: theme('radius.round'); background-color: currentColor; opacity: 0.3;
        &:before{width:9px; height: 5px; position: absolute;top:48%; left:38%; border-left:2px solid theme('color.white');border-bottom:2px solid theme('color.white'); content:"";transform: rotate(-45deg) translate(0,-78%);}
     }

    &-input:checked ~ &-action{color: theme('color.secondary'); font-weight: 600;}
    &-input:checked ~ &-action &-icon{opacity: 1;}

    }

    &.is-selected &__trigger{
        &-action{color: theme('color.secondary'); font-weight: 600;}
        &-icon{opacity: 1;}
    }
}

.selection--select{ 
    .selection__trigger {
       pointer-events: none;
    }
}

$submit-row: "submit-row";
.#{$submit-row}{
    justify-content: flex-end;

    @media(max-width:theme('breakpoints.sm','value')){
        &{
            .field_cover{display: flex; justify-content: space-between;
                & input{width:auto;}
            }
        }
    }
}


$avtar: "avtar";
$profile-media: "profile-media";

.#{$profile-media}{
    & .#{$avtar}{ border: 1px solid theme('color.gray.200');
       
        @media(max-width:theme('breakpoints.sm','value')){
        &--xlarge{width: 100%; height: 100%;}
            
        }
    }
    @media(max-width:theme('breakpoints.sm','value')){
    .buttons-group{width: 100%;display: flex;justify-content: space-between;
        .btn.btn--wide.btn.btn--small{width: 100%;min-width:1%;margin: 0;}
        }
    }
}


$form: "form";
.#{$form}{
    &__actions{ padding:theme('padding.6');
        > div{flex-wrap: nowrap;
            > div{display: inline-flex;
                &:last-child{
                    input{margin-#{theme('direction.left','value')}: 5px!important;}
                }
            }
        }
    }
    &__body{
        padding:0 theme('padding.6');
        @media(min-width:theme('breakpoints.md','value')){
            &{min-height: 30rem;}
        }
    }
}



$account-deactivation-info: "account-deactivation-info";
.#{$account-deactivation-info}{
    @media(max-width:theme('breakpoints.sm','value')){
        .btns-group{
            > .btn{display: block;text-align: center;margin: 5px 0;;}
        }
    }
}


$selection-tabs: "selection-tabs";

.#{$selection-tabs}{display:flex; align-items: center; justify-content: center; flex-wrap: wrap;overflow: auto;

    @media(max-width:theme('breakpoints.md','value')){
        &{justify-content: flex-start;flex-wrap: nowrap;}
    }
    
    &__label{ position: relative; cursor: pointer;}
    &__input{position: absolute;opacity: 0;}
    &__title{background-color: #fff; position: relative; display: flex; align-items: center; justify-content: center; flex-direction: column;padding: theme('padding.4');
        border:1px solid theme('color.gray.200');font-size:theme('font.size.lg'); font-weight: theme('font.weight.medium'); min-width:5rem; color: theme('color.gray.900');
        & span{font-size: 50%; text-transform: uppercase; font-weight:theme('font.weight.regular');}
    }
    &__input:checked + &__title{color: theme('color.secondary'); border-color: theme('color.secondary');}
}



$price-box: "price-box";

.#{$price-box}{border:1px solid theme('color.gray.200'); position: relative; margin-top: theme('margin.6');
    &__head{padding: theme('padding.4') theme('padding.6');border-bottom:1px solid theme('color.gray.200');padding-#{theme('direction.right','value')}: theme('padding.18')}
    
    &__body{padding: theme('padding.6')}
  
    & .field_label{margin-bottom: 0.5rem;}
}

  
$actions-group: "actions-group";

.#{$actions-group}{
    &{display: inline-flex; align-items: center; flex-wrap: wrap;}
    @media(max-width:theme('breakpoints.xl','value')){
        &{white-space: nowrap;}
    }

}



$form-actions: "form-actions";
.#{$form-actions}{ padding:theme('padding.6');
}


$wallet-box: "wallet-box";
.#{$wallet-box}{
    @media(max-width:theme('breakpoints.lg','value')){
        & {
            .buttons-group{ margin-top: theme('margin.4');
                & > a{border-color: theme('color.gray.300'); flex: 1; font-size: theme('font.size.sm');}
            }
        }
    }
    @media(max-width:theme('breakpoints.sm','value')){
        & {padding: theme('padding.5')!important;
            .buttons-group{flex-wrap: wrap;
                & > a{border-color: theme('color.gray.300'); flex: auto; font-size: theme('font.size.sm'); width: 100%; margin: 5px 0;display: flex;}
            }
        }
    }
}


$icon: "icon";



$flashcard: "flashcard";
.#{$flashcard}{ padding:theme('padding.6');
    &__media{width: 10rem; height: 10rem; border-radius: theme('radius.round'); display: flex; align-items: center; justify-content: center; margin: 0 auto 20px; background-color:rgba(0,0,0,0.04);}

    @media(min-width:theme('breakpoints.xl','value')){
        &{
            .buttons-group{margin-top: theme('margin.6');
                & a{width: 100%; margin-top: theme('margin.3');}
            }
