    @media(max-width:theme('breakpoints.xl','value')){
        &{display: flex; align-items: center;
            &__media{margin: 0;width: 6rem; height: 6rem; margin-#{theme('direction.right','value')}: theme('margin.6');
            .#{$icon}{
                &--xlarge{width: 2.6rem; height: 2.6rem;}
            }
        }
            .buttons-group{display: flex; align-items: center;
                & > *{margin-#{theme('direction.right','value')}: 5px;}
            }
            &__content{display: flex; align-items: center; justify-content: space-between;flex: 1;}
        }
    }

    @media(max-width:theme('breakpoints.sm','value')){
        &{display: block;
            &__media{margin: 0 auto theme('margin.4');width: 8rem; height: 8rem;
            
        }
        
            &__content{display: block;flex: unset; text-align: center;
               .buttons-group{display: flex; margin-top: theme('margin.4');
