.infobar--primary {
    background: theme('color.primary');
    color: theme('color.primary-inverse');
    overflow: hidden;
    
    &::before{
        content: "";
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        #{theme('direction.left','value')}: 0;
        background: repeating-linear-gradient(135deg, rgba(255,255,255,0.15), rgba(255,255,255,0.15) 1px, theme('color.primary') 1px, theme('color.primary') 6px);
        opacity: 0.5;
    }
    .timer{
        color: inherit;
    }

    p, span{
        color: theme('color.primary-inverse');
    }
}

.select__dropdown {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    border: none;
    min-height: 3.5rem;
    line-height: 20px;
    background: none;
