        color: #999;
    }
}

.price-box__head{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media(max-width:theme('breakpoints.sm','value')){
        & {
            flex-direction: column;
            align-items: flex-start;

            .common-slot-price {
                margin-top: 10px;
            }
        }
    }

    .arrow-toggle{
        position: absolute;
        #{theme('direction.right','value')}: 0;
        top: 50%;
        transform: translate(0, -50%);
        width: 40px;
        height: 40px;
        cursor: pointer;
        &::before{
            content: "";
            border-#{theme('direction.left','value')}: 6px solid transparent;
            border-#{theme('direction.right','value')}: 6px solid transparent;
            border-top: 7px solid theme('color.gray.600');
            #{theme('direction.right','value')}: 22px;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
        }
    }
}

 
