$table-controls: 'table-controls';
.#{$table-controls} {
    &{overflow: auto; width: 100%; display: block;  text-align: center;
        &__colum{  position: relative;
           &:before{position: absolute;inset: 0; opacity:0; content: ""; background-color: theme('color.primary');}
           &.color2{color:  theme('color.primary');
            &:before{opacity: 0.05;}
           }
           & .position-relative{z-index: 1; line-height: 1.3; white-space: nowrap;
                & .input-helper{background-color: #fff; border-color: currentColor!important;
                    &:before,&:after{background: currentColor;}
                }
                & .checkbox{color: theme('color.primary');}
            }
            &.is-selected{color:  theme('color.primary');
                &:before{opacity: 0.05;}
            }
        }
        
    }
}

.position-relative{position: relative;}


$table-sticky-scroll: 'table-sticky-scroll';

.#{$table-sticky-scroll} {
    &{
        th{padding: theme('padding.4') theme('padding.4');font-weight: theme('font.weight.medium');background-color: theme('color.white')!important; 
            box-shadow:inset 0 1px 0 0 rgba(0, 0, 0, 0.1);}
        td{padding: theme('padding.1')  theme('padding.4'); border: none;background-color: theme('color.white');}
        & .first-child{ position: sticky; left: 0; z-index: 2; top: 0; width: 100px; min-width: 100px; text-align: #{theme('direction.left','value')};}
        tbody tr:first-child td{padding-top: theme('padding.8'); box-shadow:inset 0 1px 0 0 rgba(0, 0, 0, 0.1);}
        tbody tr:last-child td{padding-bottom: theme('padding.8');box-shadow:inset 0 -1px 0 0 rgba(0, 0, 0, 0.1);}

        @media(min-width:theme('breakpoints.sm','value')){
            &{
                th{padding: theme('padding.8') theme('padding.6');}
                td{padding: theme('padding.2')  theme('padding.6'); }
                & .first-child{ width: 180px; min-width: 180px;}
            }
        }


    }
}


$lang-selection: 'lang-selection';

